import React, { useState, useEffect } from "react";
import { authCheck } from "./services/FirebaseConfig";
import { Routes, Route, Navigate } from "react-router-dom";

import Navbar from "./components/Navbar";
import Home from "./pages/Home";
import Users from "./pages/Users";
import SignIn from "./pages/SignIn";
import EditProfile from "./pages/EditProfile";

var CryptoJS = require("crypto-js");

function App() {
  const initialState = {
    user: null,
    level: "guest",
  };
  const [session, setSession] = useState(initialState);

  // eslint-disable-next-line
  useEffect(async () => {
    const token = sessionStorage.getItem("token");
    if (token) {
      if (session !== initialState) {
        return;
      }
      var bytes = CryptoJS.AES.decrypt(token, "raningu@7712219");
      var decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
      setSession(decryptedData);
    } else {
      sessionStorage.removeItem("token");
      await authCheck();
    }
  }, []);

  return (
    <div className="app-container">
      <Navbar session={session} />
      <Routes>
        <Route exact path="/" element={<Home />}></Route>
        <Route exact path="/docs"></Route>
        <Route
          exact
          path="/signin"
          element={!session.user ? <SignIn session={session} /> : <Home />}
        ></Route>
        <Route
          exact
          path="/edit_profile"
          element={
            session.user ? (
              <EditProfile session={session} />
            ) : (
              <Navigate to="/" />
            )
          }
        ></Route>
        <Route
          exact
          path="/admin"
          element={<Users session={session} />}
        ></Route>
      </Routes>
    </div>
  );
}

export default App;
