import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Alert from "@mui/material/Alert";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import IconButton from "@mui/material/IconButton";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Divider from "@mui/material/Divider";
import { GitHub, Facebook, Google } from "@mui/icons-material";
import {
  signInWithFacebook,
  signInWithGoogle,
  singInWithGithub,
} from "../services/FirebaseConfig";

const theme = createTheme();

export default function SignIn({ session }) {
  const [alert, setAlert] = React.useState(false);
  const [desc, setDesc] = React.useState("");
  const toggleShowAlert = () => {
    setAlert(true);
    setDesc("Email/Password Incorrect!");
  };
  const toggleRequiredAlert = () => {
    setAlert(true);
    setDesc("Email/Password required!");
  };

  const toggleHideAlert = () => {
    setAlert(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);

    if (data.get("email") === "" || data.get("password") === "") {
      return toggleRequiredAlert();
    }
    // eslint-disable-next-line no-console
    console.log({
      email: data.get("email"),
      password: data.get("password"),
    });
    toggleHideAlert();
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in (Administrator Only)
          </Typography>
          <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
            />
            {alert && <Alert severity="error">{desc}</Alert>}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign In
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link href="#" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>

            <Divider style={{ margin: "20px" }}>Sign-In Method</Divider>
            {/* Login Method */}
            <Grid container justifyContent="center" spacing={5}>
              <Grid item>
                <IconButton size="large" onClick={signInWithGoogle}>
                  <Google />
                </IconButton>
              </Grid>
              <Grid item>
                <IconButton size="large" onClick={signInWithFacebook}>
                  <Facebook />
                </IconButton>
              </Grid>
              <Grid item>
                <IconButton size="large" onClick={singInWithGithub}>
                  <GitHub />
                </IconButton>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
