import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { Tooltip, IconButton } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import SettingsIcon from "@mui/icons-material/Settings";
import AdminPanelSettingsIcon from "@mui/icons-material/AdminPanelSettings";
import LogoutIcon from "@mui/icons-material/Logout";
import Divider from "@mui/material/Divider";
import DescriptionIcon from "@mui/icons-material/Description";
import { NavLink } from "react-router-dom";
import { signOut } from "../services/FirebaseConfig";
import { Box } from "@mui/system";
import Avatar from "@mui/material/Avatar";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import EditIcon from "@mui/icons-material/Edit";

const useStyles = makeStyles((theme) => ({
  title: {
    flexGrow: 1,
  },
}));

const Navbar = ({ session }) => {
  // User Control
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const classes = useStyles();

  return (
    <AppBar position="static">
      <Toolbar>
        <Menu
          id="basic-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            "aria-labelledby": "basic-button",
          }}
        >
          {session.user && (
            <Box sx={{ px: 2, py: 1 }}>
              <Typography>{session.user.displayName}</Typography>
              <Typography style={{ color: "gray" }}>
                {session.level === "admin" ? "Administrator" : "User"}
              </Typography>
            </Box>
          )}
          {session.level === "admin" && (
            <NavLink
              to="/edit_profile"
              style={{ textDecoration: "none", color: "black" }}
            >
              <MenuItem
                onClick={() => {
                  handleClose();
                }}
              >
                <ListItemIcon>
                  <EditIcon fontSize="small" />
                </ListItemIcon>
                Edit Profile
              </MenuItem>
            </NavLink>
          )}
          <MenuItem
            onClick={() => {
              handleClose();
              signOut();
            }}
          >
            <ListItemIcon>
              <LogoutIcon fontSize="small" />
            </ListItemIcon>
            Logout
          </MenuItem>
        </Menu>
        <Typography variant="h6" className={classes.title}>
          <NavLink to="/" style={{ textDecoration: "none", color: "white" }}>
            Rāningu Developer
          </NavLink>
        </Typography>
        <Box>
          {session.user ? (
            <Box>
              {session.level === "admin" ? (
                <Tooltip
                  title="Documentation"
                  style={{ textDecoration: "none", color: "white" }}
                >
                  <NavLink to="/docs">
                    <IconButton>
                      <DescriptionIcon style={{ fill: "white" }} />
                    </IconButton>
                  </NavLink>
                </Tooltip>
              ) : (
                <NavLink
                  to="/docs"
                  style={{ textDecoration: "none", color: "white" }}
                >
                  <Button
                    variant="outlined"
                    style={{
                      textDecoration: "none",
                      borderColor: "white",
                      color: "white",
                    }}
                    startIcon={<DescriptionIcon />}
                  >
                    Docs
                  </Button>
                </NavLink>
              )}

              {session.level === "normal" && (
                <Divider
                  orientation="vertical"
                  flexItem
                  style={{ display: "inline" }}
                  sx={{ px: 1 }}
                />
              )}

              {session.level === "admin" ? (
                <Tooltip
                  title="Admin Console"
                  style={{ textDecoration: "none", color: "white" }}
                >
                  <NavLink to="/admin">
                    <IconButton>
                      <SettingsIcon style={{ fill: "white" }} />
                    </IconButton>
                  </NavLink>
                </Tooltip>
              ) : (
                <NavLink to="/edit_profile" style={{ textDecoration: "none" }}>
                  <Button
                    variant="contained"
                    style={{
                      color: "#3f51b5",
                      backgroundColor: "white",
                    }}
                    startIcon={<EditIcon />}
                  >
                    Edit Profile
                  </Button>
                </NavLink>
              )}

              <Tooltip title="User Control">
                <IconButton onClick={handleClick}>
                  <Avatar src={session.user.photoURL} />
                </IconButton>
              </Tooltip>
            </Box>
          ) : (
            <Box position="flex">
              <NavLink
                to="/docs"
                style={{ textDecoration: "none", color: "white" }}
              >
                <Button variant="text" color="inherit">
                  Documentation
                </Button>
              </NavLink>
              |
              <Tooltip title="Sign In (Administrator)">
                <NavLink to="/signin">
                  <IconButton>
                    <AdminPanelSettingsIcon style={{ fill: "white" }} />
                  </IconButton>
                </NavLink>
              </Tooltip>
            </Box>
          )}
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
